import React, { Component } from 'react';
import Table from './Table';
import Form from './Form';


class App extends Component {

  state = {
    records: []
  }

  removeRecord = index => {
    const { records } = this.state;
  
    this.setState({
      records: records.filter((record, i) => {
        return i !== index;
      })
    })
  }

  addRecord = record => {
    this.setState({ records: [...this.state.records, record] })
  }

  render() {

    return (
      <div className="container">
        <Table rowsData={this.state.records} removeRecord={this.removeRecord} />
        <Form addRecord={this.addRecord} />
      </div>
    )
  }
}

export default App;
