import React, { Component } from 'react';

class Table extends Component {
  render() {
    const { rowsData, removeRecord } = this.props;
    return (
      <table>
        <TableHeader />
        <TableBody rowsD={rowsData} removeRecord={removeRecord} />
      </table>
    )
  }
}

const TableHeader = () => {
  return (
    <thead>
      <tr>
        <th>Name</th>
        <th>Job</th>
        <th>Remove</th>
      </tr>
    </thead>
  )
};

const TableBody = props => {
  const rows = props.rowsD.map((row, index) => {
    return (
      <tr key={index}>
        <td>{row.name}</td>
        <td>{row.job}</td>
        <td>
          <button onClick={() =>props.removeRecord(index)}>Delete</button>
        </td>
      </tr>
    )
  });

  return <tbody>{rows} </tbody>
}

export default Table;